}

.navigation {
  position: relative;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #fff;
  border-top: 1px solid #eaeaea;
  z-index: 10;
}

.navigation ul::after {
  content: '';
  display: block;
  clear: both;
}

.navigation ul li {
  float: left;
  width: 20%;
  text-align: center;
}

.navigation ul li a {
  display: block;
  height: 80px;
  padding-top: 14px;
  font-size: 10px;
  color: rgba(0, 0, 0, 0.87);
  letter-spacing: -0.2px;
}

.navigation ul li a:before {
  content: '';
  display: block;
  margin: 0 auto 8px;
  background: url(https://static.wehago.com/imgs/ionyou/sp_ionyou.png) no-repeat;
  background-size: 200px 200px;
  -webkit-background-size: 200px 200px;
}

.navigation ul li.navigation_friend a:before {
  width: 18px;
  height: 19px;
  background-position: 0 -107px;
}

